// @flow
import React from 'react'
import styles from './Title.css'
import { withRouter } from 'react-router'

const switchTitle = (path: string) => {
  if (path.startsWith('/operator')) return ''
  if (path.startsWith('/store')) return ''
  if (path.startsWith('/user-center')) return ''
  if (path.startsWith('/supplement')) return ''
  if (path.startsWith('/login')) return ''
  if (path.startsWith('/devices')) return ''
  if (path.startsWith('/stockouts')) return ''
  if (path.startsWith('/replenish')) return ''
  return ''
}

const hideNames = [ '', '', '', '', '' ]

class Title extends React.Component<{match: Object}, {}> {
  componentWillReceiveProps (nextProps) {
    const { match: { path } } = nextProps
    document.title = switchTitle(path)
  }
  render () {
    const { match: { path } } = this.props
    const title = switchTitle(path)
    const hideDetail = hideNames.includes(title)
    return (
      <div>
        {
          hideDetail
          ? false
          : (
            <div>
              <img src='./VEM@3x.png' alt='cover' style={{ width: '100vw' }}/>
              {
                title === ''
                ? <div className={styles['login-tips']} style={{ backgroundImage: 'url(./logo_y.png)' }}/>
                : <div className={styles.tips}>{title}</div>
              }

            </div>
          )
        }
      </div>
    )
  }
}
export default withRouter(Title)



